html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}a{text-decoration:none;color:inherit}button{padding:0;border:none;background:none;outline:none}input{border:0;outline:none}*{box-sizing:border-box;user-select:none;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;-webkit-touch-callout:none;-webkit-tap-highlight-color:transparent}*::-webkit-scrollbar{width:0;height:0;appearance:none}@font-face{src:url("@/assets/fonts/FjallaOne-Regular.ttf") format("truetype");font-family:"Fjalla One";font-style:normal;font-weight:normal}@keyframes bounce-back-and-forth{0%{transform:translate(0, 0)}50%{transform:translate(0, 15px)}100%{transform:translate(0, 0)}}@keyframes bounce-in{0%{opacity:0;transform:translate3d(-50%, 300%, 0)}33%{opacity:1}66%{transform:translate3d(-50%, -75%, 0)}100%{transform:translate3d(-50%, -50%, 0)}}html,body,#root,#app{width:100%;height:100%;margin:0 auto;line-height:normal;font-family:"Noto Sans TC",sans-serif;touch-action:manipulation}html,body{background-color:#294c60;overflow:hidden}#root{max-width:450px;background-color:#001b2e;box-shadow:5px 5px 10px rgba(0,0,0,.2);display:flex;flex-direction:column}#root #app{width:100%;height:100vh;position:relative;overflow:scroll}